#contactPage {
    min-height: calc(100vh-48px);
    width: 100vw;
    max-width: 80%;
    margin: 0 auto;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contactPageTitle {
    font-size: 36px;
    margin: 42px;
}

.contactDescription {
    padding: 24px;
    font-size: medium;
    font-weight: 300;
}

.contactForm {
    margin: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 75vw;
}

.name, .email, .msg {
    font-size: medium;
    width: 100%;
    max-width: 75%;
    margin: 12px;
    padding: 12px 24px;
    color: white;
    border: none;
    border-radius: 12px;
    background: rgb(40, 40, 40);
}

.submitBtn {
    background:white;
    border: none;
    border-radius: 12px;
    margin: 36px;
    padding: 12px 54px;
}

.links {
    display: flex;
    flex-wrap: wrap;
}

.link {
    object-fit: cover;
    height: 54px;
    width: 54px;
    margin: 0 12px;
}